:root {
  /* Light Mode */

  --BgColor_LightMode: #f5f5f5;
  --NavbarColor_LightMode: #fdfcfc;

  --TitleColor_LightMode: #037784;
  --BtnColor_LightMode: #74512d;
  --HoverColor_LightMode: #af8f6f;
  --LinkColor_LightMode: #f8f4e1;
  /* Dark Mode */

  --BgColor_DarkMode: #242d3d;
  --NavbarColor_DarkMode: #1a202c;
  --PColor_DarkMode: #ecc94b;
  --TitleColor_DarkMode: #63b3ed;
  --BtnColor_DarkMode: #f6ad55;
  --HoverColor_DarkMode: #a0aec0;
  --LinkColor_DarkMode: #9f7aea;
}

/* Light-Mode */
.bodyAbout,
.bodyPackages {
  background-color: var(--BgColor_LightMode);
}
/* About  */ /* Cards and FindTheOne */

.sandBoxContainer {
  background-color: #eee;
}
.dark-mode .bodyAbout,
.dark-mode .bodyFindTheOne,
.dark-mode .bodyCards,
.dark-mode .bodyLoading,
.dark-mode .sandBoxContainer {
  background-color: var(--BgColor_DarkMode);
  transition: background-color 0.8s, color 0.3s ease-in;
}
.dot-spinner__dot::before {
  background-color: #183153;
  transition: background-color 0.8s, color 0.3s ease-in-out;
}
.dark-mode .dot-spinner__dot::before {
  background-color: wheat;
  transition: background-color 0.8s, color 0.3s ease-in-out;
}

h1,
h2 {
  color: var(--TitleColor_LightMode);
}

p {
  color: var(--PColor_LightMode);
}

nav.container_nav,
.link_list.open {
  background-color: var(--NavbarColor_LightMode);
}

.link {
  color: #575757;
}

.link:hover {
  color: var(--HoverColor_LightMode);
}
/* DarkMode */
.dark-mode h1 {
  color: var(--TitleColor_DarkMode);
}
.dark-mode h2 {
  color: var(--TitleColor_DarkMode);
}
.dark-mode p {
  color: var(--TitleColor_DarkMode);
}
.dark-mode nav.container_nav,
.dark-mode footer.bodyFooter,
.dark-mode .link_list.open {
  background-color: var(--NavbarColor_DarkMode);
}
.dark-mode .link {
  color: var(--LinkColor_DarkMode);
}
.dark-mode .link:hover {
  color: var(--HoverColor_DarkMode);
}

